import React, { useState } from 'react';

export default function Header() {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [isDropdownOpen, setIsDropdownOpen] = useState(false);

    return (
        <div className="fixed top-0 w-full z-50">
            {/* Top bar */}
            <div className="bg-blue-600 hidden lg:block">
                <div className="max-w-7xl mx-auto">
                    <div className="flex justify-between items-center px-4 py-2">
                        <div className="flex items-center space-x-6">
                            <div className="flex items-center text-white text-sm">
                                <i className="fas fa-map-marker-alt mr-2 text-gray-300"></i>
                                <a href="#" className="text-white hover:text-gray-200">123 Street, New York</a>
                            </div>
                            <div className="flex items-center text-white text-sm">
                                <i className="fas fa-envelope mr-2 text-gray-300"></i>
                                <a href="#" className="text-white hover:text-gray-200">Email@Example.com</a>
                            </div>
                        </div>
                        <div className="flex items-center space-x-1 text-sm">
                            <a href="#" className="text-white hover:text-gray-200">Privacy Policy</a>
                            <span className="text-white">/</span>
                            <a href="#" className="text-white hover:text-gray-200">Terms of Use</a>
                            <span className="text-white">/</span>
                            <a href="#" className="text-white hover:text-gray-200">Sales and Refunds</a>
                        </div>
                    </div>
                </div>
            </div>

            {/* Main navigation */}
            <div className="bg-white shadow-sm">
                <div className="max-w-7xl mx-auto px-4">
                    <nav className="flex items-center justify-between h-16">
                        {/* Logo */}
                        <a href="index.html" className="flex items-center">
                            <h1 className="text-blue-600 text-2xl font-bold">Fruitables</h1>
                        </a>

                        {/* Mobile menu button */}
                        <button
                            className="lg:hidden p-2 text-blue-600"
                            onClick={() => setIsMenuOpen(!isMenuOpen)}
                        >
                            <i className="fa fa-bars text-xl"></i>
                        </button>

                        {/* Desktop navigation */}
                        <div className="hidden lg:flex items-center space-x-8">
                            <div className="flex items-center space-x-6">
                                <a href="index.html" className="text-gray-700 hover:text-blue-600 font-medium border-b-2 border-blue-600">
                                    Home
                                </a>
                                <a href="shop.html" className="text-gray-700 hover:text-blue-600 font-medium">
                                    Shop
                                </a>
                                <a href="shop-detail.html" className="text-gray-700 hover:text-blue-600 font-medium">
                                    Shop Detail
                                </a>
                                
                                {/* Dropdown */}
                                <div className="relative">
                                    <button
                                        className="text-gray-700 hover:text-blue-600 font-medium flex items-center"
                                        onClick={() => setIsDropdownOpen(!isDropdownOpen)}
                                    >
                                        Pages
                                        <i className="fas fa-chevron-down ml-1 text-xs"></i>
                                    </button>
                                    {isDropdownOpen && (
                                        <div className="absolute top-full left-0 mt-1 w-48 bg-gray-600 shadow-lg rounded-none z-50">
                                            <a href="cart.html" className="block px-4 py-2 text-white hover:bg-gray-700">
                                                Cart
                                            </a>
                                            <a href="chackout.html" className="block px-4 py-2 text-white hover:bg-gray-700">
                                                Checkout
                                            </a>
                                            <a href="testimonial.html" className="block px-4 py-2 text-white hover:bg-gray-700">
                                                Testimonial
                                            </a>
                                            <a href="404.html" className="block px-4 py-2 text-white hover:bg-gray-700">
                                                404 Page
                                            </a>
                                        </div>
                                    )}
                                </div>
                                
                                <a href="contact.html" className="text-gray-700 hover:text-blue-600 font-medium">
                                    Contact
                                </a>
                            </div>

                            {/* Right side icons */}
                            <div className="flex items-center space-x-4">
                                {/* Search button */}
                                <button className="w-10 h-10 border border-gray-300 rounded-full bg-white flex items-center justify-center hover:bg-gray-50">
                                    <i className="fas fa-search text-blue-600"></i>
                                </button>
                                
                                {/* Shopping cart */}
                                <a href="#" className="relative">
                                    <i className="fa fa-shopping-bag text-2xl text-gray-700"></i>
                                    <span className="absolute -top-2 -right-2 bg-gray-600 text-white rounded-full w-5 h-5 flex items-center justify-content text-xs">
                                        3
                                    </span>
                                </a>
                                
                                {/* User icon */}
                                <a href="#" className="text-gray-700">
                                    <i className="fas fa-user text-2xl"></i>
                                </a>
                            </div>
                        </div>

                        {/* Mobile navigation */}
                        {isMenuOpen && (
                            <div className="absolute top-full left-0 w-full bg-white shadow-lg lg:hidden">
                                <div className="px-4 py-2 space-y-2">
                                    <a href="index.html" className="block py-2 text-gray-700 hover:text-blue-600 font-medium border-l-2 border-blue-600 pl-3">
                                        Home
                                    </a>
                                    <a href="shop.html" className="block py-2 text-gray-700 hover:text-blue-600 font-medium">
                                        Shop
                                    </a>
                                    <a href="shop-detail.html" className="block py-2 text-gray-700 hover:text-blue-600 font-medium">
                                        Shop Detail
                                    </a>
                                    <div className="py-2">
                                        <button
                                            className="text-gray-700 hover:text-blue-600 font-medium w-full text-left"
                                            onClick={() => setIsDropdownOpen(!isDropdownOpen)}
                                        >
                                            Pages
                                        </button>
                                        {isDropdownOpen && (
                                            <div className="ml-4 mt-2 space-y-1">
                                                <a href="cart.html" className="block py-1 text-gray-600 hover:text-blue-600">
                                                    Cart
                                                </a>
                                                <a href="chackout.html" className="block py-1 text-gray-600 hover:text-blue-600">
                                                    Checkout
                                                </a>
                                                <a href="testimonial.html" className="block py-1 text-gray-600 hover:text-blue-600">
                                                    Testimonial
                                                </a>
                                                <a href="404.html" className="block py-1 text-gray-600 hover:text-blue-600">
                                                    404 Page
                                                </a>
                                            </div>
                                        )}
                                    </div>
                                    <a href="contact.html" className="block py-2 text-gray-700 hover:text-blue-600 font-medium">
                                        Contact
                                    </a>
                                </div>
                            </div>
                        )}
                    </nav>
                </div>
            </div>
        </div>
    );
}